.portfolio {
  padding: 4%;
  text-align: center; }

.heading {
  background: #fff;
  padding: 1%;
  text-align: left;
  box-shadow: 0 0 4px 0 #545b62; }

.heading img {
  width: 10%; }

.bio-info {
  background: #fff;
  box-shadow: 0 0 4px 0 #b0b3b7; }

.bio-image {
  text-align: center; }

.bio-image img {
  width: 250px;
  height: 250px;
